<template>
    <div class="login-box">
        <el-form ref="form" :model="form" :rules="rules" label-width="120px" status-icon>
            <el-form-item label="Username" prop="username">
                <el-input placeholder="Please input username" autocomplete="off" v-model="form.username" clearable></el-input>
            </el-form-item>
            <el-form-item label="Password" prop="password">
                <el-input placeholder="Please input password" autocomplete="off" v-model="form.password" clearable show-password></el-input>
            </el-form-item>
            <el-form-item>
                <div class="button-holder">
                    <el-button type="success" @click="onSubmit" icon="el-icon-check" circle></el-button>
                    <router-link to="/"><el-button type="info" icon="el-icon-s-home" circle></el-button></router-link>
                </div>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
import {db} from "./../firebase"
import router from "./../router"
export default {
    data() {
        var validateUsername = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('Please input username again!'));
            } else {
                db.collection('portfolio').doc('credential').get().then((doc)=>{
                    if (this.form.username === doc.data().username) {
                        callback();
                    } else {
                        callback(new Error("Username does not exist!"));
                    }
                })
            }
        };
        var validatePassword = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('Please input password again!'));
            } else {
                db.collection('portfolio').doc('credential').get().then((doc)=>{
                    if (this.form.password === doc.data().password) {
                        callback();
                    } else {
                        callback(new Error("Wrong password!"));
                    }
                })
            }
        }
        return {
            form: {
                username: "",
                password: ""
            },
            rules: {
                username: [
                    { required: true, message: "Please enter username!", trigger: 'blur' },
                    { validator: validateUsername, trigger: 'change' }
                ],
                password: [
                    { required: true, message: "Please enter password!", trigger: 'blur' },
                    { validator: validatePassword, trigger: 'submit'}
                ]
            }
        }
    },
    methods: {
        onSubmit() {
            this.$refs.form.validate((valid)=>{
                if (valid) {
                    alert("Login successfully!");
                    window.localStorage.setItem("loggedin", "true");
                    router.push('/edit');
                } else {
                    return false;
                }
            })
        }
    }
}
</script>

<style scoped>
.button-holder {
    display: flex;
    justify-content: center;
    padding-right: 120px;
}
.login-box {
    margin-left: 20%;
    margin-right: 20%;
    margin-top: 20px;
}

.el-button {
    margin: 1rem;
}

@media(max-width: 768px){
    .login-box{
        margin-left: 20px;
        margin-right: 20px;
    }
    .el-button {
        margin: 0rem;
        margin-right: 20px;
    }
}
</style>